<template lang="pug">
div.container-fluid
  div#newIntervalForm
    div.row
      label(for='startTime') Start: 
      button.btn.btn-danger(
        @click='decrementStart'
      ) &minus;
      input#startTime(
        type='number'
        :min='earliestTime'
        :max='finishTime - 1'
        v-model='startTime'
      )
      button.btn.btn-success(
        @click='incrementStart'
      ) &plus;
    div.row
      br
      label(for='finishTime') Finish:
      button.btn.btn-danger(
        @click='decrementFinish'
      ) &minus;
      input#finishTime(
        type='number'
        :min='startTime + 1'
        :max='latestTime'
        v-model='finishTime'
      )
      button.btn.btn-success(
        @click='incrementFinish'
      ) &plus;
    div.row
      br
      label(for='textInput') Interval:
      input#textInput(
        type='text'
        v-model='intervalText'
        @keyup.enter='createNewInterval'
        )
  br
  div.row
    div.col-xs-12
      nice-button.btn-primary(@click='createNewInterval') Add Interval
</template>

<script>
import Vuex from 'vuex';
import NiceButton from '../nice-things/Nice-Button';

export default {
  components: {
    NiceButton,
  },
  props: [
    'index',
  ],
  data() {
    return {
      startTime: 0,
      finishTime: 1,
    };
  },
  computed: {
    ...Vuex.mapState([
      'problemSize',
      'earliestTime',
      'latestTime',
    ]),
    intervalText: {
      get() {
        return `${this.startTime} ${this.finishTime}`;
      },
      set(newVal) {
        let correct = true;
        let start;
        let finish;
        correct = correct && newVal.split(' ').length === 2;
        if (correct) {
          start = Number.parseInt(newVal.split(' ')[0], 10);
          finish = Number.parseInt(newVal.split(' ')[1], 10);
        }
        correct = correct && start < finish;
        if (correct) {
          this.startTime = Math.max(this.earliestTime, start);
          this.finishTime = Math.min(this.latestTime, finish);
        } else {
          // do nothing
        }
      },
    },
  },
  methods: {
    incrementStart() {
      // If there's enough room
      this.startTime = Math.max(this.startTime + 1, this.earliestTime);
      // check to see if start === finish
      if (this.startTime >= this.finishTime) {
        this.incrementFinish();
      }
    },
    incrementFinish() {
      this.finishTime = Math.min(this.finishTime + 1, this.latestTime);
    },
    decrementStart() {
      this.startTime = Math.max(this.startTime - 1, this.earliestTime);
    },
    decrementFinish() {
      this.finishTime = Math.min(this.finishTime - 1, this.latestTime);
      if (this.finishTime <= this.startTime) {
        this.decrementStart();
      }
    },
    createNewInterval() {
      this.$store.dispatch('addInterval', { start: this.startTime, finish: this.finishTime });
    },
  },
};
</script>

<style scoped>

#newIntervalForm label {
  text-align: right;
  width: 25%;
  margin-right: 2.5%;
  margin-left: 2.5%;
}
#newIntervalForm input[type=number] {
  width: 25%;
  margin-left: 2.5%;
  margin-right: 2.5%;
  text-align: center;
}
#newIntervalForm label, input[type=number] {
  font-size: 1.2em;
}
/* Remove the arrows from the number input */
#newIntervalForm input[type=number]::-webkit-inner-spin-button,
#newIntervalForm input[type=number]::-webkit-outer-spin-button {
    -moz-appearance:textfield;
    -webkit-appearance: none;
    margin: 0;
}

input#textInput {
  font-size: 1.2em;
  width: 50%;
  margin-left: 10px;
  padding-left: 3px;
}

#newIntervalForm button {
  width: 15%;
}
</style>



